.side-drawer {
  height: 100%;
  background: rgba(248, 226, 220, 0.911);
  box-shadow: 2px 0px 5px rgba(0, 0, 0, 0.5);
  position: fixed;
  top: 0;
  left: 0;
  width: 70%;
  max-width: 220px;
  z-index: 200;
  transform: translate(-100%);
}

.side-drawer ul {
  height: 100%;
  list-style: none;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.side-drawer li {
  margin: 0.5rem 0;
}

.side-drawer a {
  color: rgb(151, 178, 183);
  text-decoration: none;
  font-size: 1.2rem;
}

.side-drawer a:hover,
.side-drawer a:active {
  color: rgb(49, 54, 61);
}

.side-drawer.open {
  transform: translateX(0);
}
